@import '../../scss/func.scss';

.corusel{
    &-main-contaner{
        margin-inline: auto;
        height: vw(810, 1920);
        width: vw(1600, 1920);


        @media (max-width: 820px) {
            height: vw(810, 640);
            margin-bottom: vw(70, 640);
            width: auto;
        }

        @media (max-width: 420px) {
            height: vw(520, 420);
            margin-bottom: vw(70,  420);
            width: auto;
        }
    }

    &-window{
        height: 100%;
        width: 100%;
        overflow: hidden;
    }

    &-all-pages-container{
        height: 100%;
        display: flex;

        transition: translate;
        transition-property: transform;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
    }

    &-arrows{
        display: flex;
        justify-content: center;
        align-items: center;
        

        &-item{
            cursor: pointer;
            margin-top: vw(15,1920);
            margin-right: vw(60, 1920);

            transition-duration: 500ms;

            width: vw(30, 1920);

            @media (max-width: 820px) {

                margin-top: vw(15,640);
                margin-right: vw(60, 640);
    
                width: vw(30, 640);
            }

            @media (max-width: 420px) {
                
                margin-top: vw(15, 420);
                margin-right: vw(20, 420);
    
                width: vw(30, 420);
            }

            &:hover{
                scale: 1.1;
                transition-duration: 300ms;
            }

            &:active{
                scale: 0.9;
                transition-duration: 300ms;
            }
        }
    }
}