@import '../scss/func.scss';

.item{
    display: flex;
    user-select: none;
}

.item-s{
    position: relative;

    img{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        user-select: none;
    }

    &__content{
        user-select: none;

        &-text{
            width: vw(510, 1920);
            margin-left: vw(200, 1920);
            margin-top: vw(250, 1920);

            @media (max-width: 820px) {
                width: vw(520, 640);
                margin-left: vm(10, 640);
                margin-top: 50%;
            }

            @media (max-width: 420px) {
                margin-top: 50%;
            }
        }

        &-img{
            img{
                width: vw(700, 1920);
                margin-left: vw(800, 1920);

                @media (max-width: 820px) {
                    display: none;
                }
            }
        }

        &-img-c{
            img{
                width: vw(700, 1920);
                height: vw(440, 1920);
                margin-left: vw(800, 1920);
                margin-top: vw(270, 1920);
                user-select: none;

                @media (max-width: 820px) {
                    display: none;
                }
            }
        }

        &-hmb{
            margin-bottom: vw(14, 1920);

            @media (max-width: 820px) {
                margin-bottom: vw(30, 640);
            }

            @media (max-width: 420px) {
                margin-bottom: vw(20, 420);
            }
        }

        &-pmb{
            margin-bottom: vw(40, 1920);

            @media (max-width: 820px) {
                margin-bottom: vw(40, 640);
            }

            @media (max-width: 420px) {
                margin-bottom: vw(40, 420);
            }
        }
    }
}
