@import 'config.scss';
@import 'func.scss';

body{
    background-color: #000;
    font-family: 'GhotamPro';
}


@font-face {
    font-family: 'GhotamPro';
    src: url('../fonts/gothampro_regular.ttf');
}

:root{
    --p-gary: #C0C0C0
}

.container{
    max-width: vw(1600, 1920);
    margin: 0 auto;

    @media (max-width: 820px) {
        max-width: 100%;
    }
}

.header{
    font-size: vw(128, 1920);
    font-weight: 800;

    @media (max-width: 820px) {
        font-size: vw(64, 640);
    }

    @media (max-width: 420px) {
        font-size: vw(32, 420);
    }
}

.subtitle{
    font-size: vw(64, 1920);
    font-weight: 500;

    @media (max-width: 820px) {
        font-size: vw(32, 640);
    }

    @media (max-width: 420px) {
        font-size: vw(24, 420);
    }
}

.paragraph{
    font-size: vw(16, 1920);

    @media (max-width: 820px) {
        font-size: vw(16, 640);
    }

    @media (max-width: 420px) {
        font-size: vw(16, 420);
    }

    &-gray{
        color: var(--p-gary);

        font-size: vw(16, 1920);

        @media (max-width: 820px) {
            font-size: vw(16, 640);
        }
    
        @media (max-width: 420px) {
            font-size: vw(16, 420);
        }
    }
}
