@import '../../scss/func.scss';


.footer-main{
    margin-top: vw(70, 1920);
    width: 100%;

    @media (max-width: 420px) {
        position: absolute;
        left: 0;
        bottom: 0;
        
    }

}

.footer-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-inline: vw(10, 1920);
    margin-bottom: vw(20, 1920);

    @media (max-width: 820px) {
        padding-inline: vw(10, 640);
        margin-bottom: vw(20, 640);
    }

    @media (max-width: 420px) {
        padding-inline: vw(10, 420);
        margin-bottom: vw(20, 420);
    }
}

.footer-email{
    display: flex;
    margin-bottom: vw(10, 1920);

    p{
        margin-left: vw(13, 1920);

        @media (max-width: 820px) {
            margin-left: vw(13, 640);
        }

        @media (max-width: 420px) {
            margin-left: vw(13, 420);
        }
    }
}

.footer-number{
    display: flex;
    margin-bottom: vw(10, 1920);

    p{
        margin-left: vw(13, 1920);

        @media (max-width: 820px) {
            margin-left: vw(13, 640);
        }

        @media (max-width: 420px) {
            margin-left: vw(13, 420);
        }
    }
}

.footer-image{
    img{
        width: vw(100, 1920);
        height: auto;


        @media (max-width: 820px) {
            width: vw(100, 640);
            height: auto;
        }
    }
}