@import '../../scss/func.scss';

.cap{
    margin-top: vw(35, 1920);
    margin-bottom: vw(20, 1920);

    @media (max-width: 820px) {
        margin-top: vw(35, 640);
        margin-bottom: vw(20, 640);
    }

    @media (max-width: 420px) {
        margin-top: vw(35, 420);
        margin-bottom: vw(20, 420);
    }


    &-content{
        @media (max-width: 820px) {
            padding-inline: vw(15, 640);
        }
    
        @media (max-width: 420px) {
            padding-inline: vw(15, 420);
        }
    
    }
}